.App{
  height: 100vh;
  .sections{
    width: 100%;
    height: calc(100vh - 0px);
    background-color: lightsalmon;
    position: relative;
    top: 0px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;
    &::-webkit-scrollbar{
      display: none;
    }

    >*{width: 100vw;
    height: calc(100vh - 0px);
  scroll-snap-align: start;}
  }
}